import React from 'react';
import Home from "./components/Home";
import Login from "./components/Login";
import Navbar from "./components/Navbar";
import Profile from "./components/Profile";
import Register from "./components/Register";
import Post from  "./components/Post";
import EditPost from  "./components/EditPost";
import './App.css';
import { useEffect, useState } from "react";
import {Routes,Route} from 'react-dom';

export const API = "https://strangers-things.herokuapp.com/api/2206-vpi-rm-web-pt";
const App = () => {
  const [posts, setPosts] = useState([]);
  const [token, setToken] = useState("");
  const [user, setUser] = useState({});


    //fetchPosts
    const fetchPosts = async () => {
      const response = await fetch(`${API}/posts`);
      const info = await response.json();
      if (info.success){
        setPosts(info.data.posts);
      }
    };
    const fetchUser = async() => {
      if(localStorage.getItem("token")) {
      setToken(localStorage.getItem("token"));
      }

      if (!token) {
        return;
      }
 

      const response= await fetch(`${API}/users/me`, {
  headers: {
    'Content-Type': 'application/json',
    Authorization: `Bearer ${token}`,
  },
}
); 
const info = response.json();
console.log(info);
setUser(info.data);
    };

    useEffect(() => {
      fetchPosts();
      fetchUser();
    }, [token]);


  return(
    <>
    <Navbar token={token} setToken={setToken} user={user} />

     <Routes>
      <Route element={<Home />} path="/" />
      <Route element={<posts post={posts} />} path="posts" />
      <Route element={<Post />} path="posts/:postId/*" />

      <Route element={<EditPost />} path= "posts/:postId" />

      <Route element={<Login setToken={setToken} />} path="/login" />
      <Route element={<Register />} path="/register" />
      <Route element={<Profile user={user} />} path="/profile" />
     </Routes>
    </>
  );
};

export default App;
